import React from "react";
import { graphql } from "gatsby";
import { Helmet } from "react-helmet"
import Layout from "../layouts/Layout";
import PortfolioList from '../components/Portfolio/presentation/PortfolioList';
import { PageHeader } from "../components/Content";
import '../components/Portfolio/Portfolio.css';

const Portfolio = props => {
    return (
        <Layout>
            <Helmet>
                <title>Case studies of Michael Lisboa's work.</title>
            </Helmet>
            <PageHeader
                theme="#26a69a"
                title="Brand. Experience. Code."
                subtitle="Case studies of my work."
            />
            <PortfolioList data={props.data} />
        </Layout>
    )
}

export default Portfolio;
export const pageQuery = graphql`
    query pageQuery {
        allContentfulPortfolioDetail(sort: {
            order: DESC,
            fields: entryDate
        }) {
            edges {
                node {
                    entryCategory
                    entryClient
                    entryDate
                    entryDescription
                    entrySlug
                    entryTileImage {
                        file {
                            url
                        }
                    }
                    entryTopic
                }
            }
        }
    }
`
