import React from 'react';
import PortfolioListItem from './PortfolioListItem';
import Contact from '../../Contact';

const hiddenCases = ['citibank', 'thailand-fintech-app'];

const PortfolioList = ({data}) => {
    const posts = data.allContentfulPortfolioDetail.edges;

    const portfolioNodes = posts
        .sort((a, b) => new Date(b.entryDate).getTime() - new Date(a.entryDate).getTime())

    return (
        <>
        <section className="uk-section">
            <div className="uk-container uk-container-small">
                <div className="portfolio-items uk-child-width-1-2@s"
                    data-uk-grid
                    data-uk-height-match="target: .uk-card-body">
        {portfolioNodes.map((post, i) =>
            !hiddenCases.includes(post.node.entrySlug) &&

                        <PortfolioListItem
                            key={i}
                            portfolioItem={post}
                        />
        )}

            </div>
        </div>
        </section>
        <Contact />
        </>
    );
}

export default PortfolioList;
